import React from 'react';
import Axios from 'axios';
import './App.css';

//exec 2.7-2.10 FINISHED
class App extends React.Component {
  constructor() {
    super()
    this.state = {
      persons: [],
      newName: '',
      newNumber: ''
    }
    console.log('constructor')
  }

  addName = (event) => {
    event.preventDefault()
    
    const nameObject = {
      name: this.state.newName,
      number: this.state.newNumber
    }  

    if (this.state.persons.some(n => n.name === this.state.newName)) {
      alert('Nimi on jo luettelossa!')  
    } else {
    
    Axios
      .post('http://localhost:3001/api/persons', nameObject)
      .then(response => {
        this.setState({
          persons: this.state.persons.concat(response.data),
          newName: '',
          newNumber: ''          
        })
      })   
    }   
  }

  componentDidMount() {
    console.log('did mount')
    Axios
      .get('http://localhost:3001/api/persons')
      .then(response => {
        console.log('promise fulfilled')
        this.setState({ persons: response.data })
      })
  } 

  handleNameChange = (event) => {
    this.setState({ newName: event.target.value })
  }

  handleNumberChange = (event) => {
    this.setState({ newNumber: event.target.value })
  }

  popWindow = (id) => {
    console.log('popped Window')
  
    const person = this.state.persons.find(n => n.id === id)
    if (window.confirm("Poistetaanko " + person.name)) {
       
      Axios
      .delete(`http://localhost:3001/api/persons/${id}`)
      .then(
        console.log('removed name'),
        this.setState({ persons: this.state.persons.filter(n => n !== person) })  
        )
    } 
  }


  render() {
    console.log('render')

    return (
      <div>
        <h2>Puhelinluettelo</h2>
        <Form addName={this.addName} newName={this.state.newName} 
        handleNameChange={this.handleNameChange} newNumber={this.state.newNumber} handleNumberChange={this.handleNumberChange} />
        <h2>Numerot</h2>
        <Table persons={this.state.persons} popWindow={this.popWindow} />
      </div>
    );
  
  }
}

const Table = (props) => {
  return (
    <table>
            <thead>
            <tr>
              <td>{props.persons.map(person => <p key={person.name}>{person.name}</p>)}</td>
              <td>{props.persons.map(person => <p key={person.name}>{person.number}</p>)}</td>
              <td>{props.persons.map(person => <p key={person.name}><button onClick={() => props.popWindow(person.id)}>poista</button></p>)}</td>
            </tr>
            </thead>
          </table>
  )

}

const Form = (props) => {
  return (
    <form onSubmit={props.addName}>
      <div>
        nimi: 
      <input 
        value={props.newName} 
        onChange={props.handleNameChange}
        />
      </div>
      <div>
        numero:
      <input
        value={props.newNumber}
        onChange={props.handleNumberChange}
      />
      </div>
      <div>
        <button type="submit">lisää</button>
      </div>
    </form>
  )

}

export default App

